import { ISectionProps } from "../ISectionProps";
import classes from "./experience.module.css";
import { Chrono } from "react-chrono";

export default function Experience(props: ISectionProps) {
  const items = [
    {
      title: "2019 - 2023  Bachelor Degree",
      cardTitle: "Bialystok's University of Technology Site",
      url: "https://pb.edu.pl/en",
      cardSubtitle: "Bachelor degree in Computer Science",
      media: {
        type: "IMAGE",
        source: {
          url: "https://bpnt.bialystok.pl/Api/Files/Get//sites/bpntportal/BPNTPortal_Pictures/d24ae63626e14d6ba3ca8f9bf48c8d71/PB%20logo.png",
        },
      },
    },
    {
      title: "2022  Internship",
      cardTitle: "Sii Site",
      url: "https://sii.pl/en",
      cardSubtitle: "Microsoft Dynamics F&O intern",
      media: {
        type: "IMAGE",
        source: {
          url: "data:image/png;base64,",
        },
      },
    },
    {
      title: "2022 - 2023 Junior Developer",
      cardTitle: "OneDynamics Site",
      url: "https://theonedynamics.com",
      cardSubtitle: "Microsoft Dynamics CRM junior developer",
      media: {
        type: "IMAGE",
        source: {
          url: "data:image/jpeg;base64,",
        },
      },
    },
    {
      title: "2023 - FullStack Developer",
      cardTitle: "CodeFellas",
      url: "https://codefellas.com",
      cardSubtitle: "Full stack developer",
      media: {
        type: "IMAGE",
        source: {
          url: "data:image/png;base64,",
        },
      },
    },
  ];

  const isActiveClass = props.isActive ? classes.active : "";

  return (
    <section style={props.style} className={classes.Experience + " " + isActiveClass} id={experienceSectionId}>
      <h2>Experience</h2>
      <Chrono
        items={items}
        theme={{
          primary: "rgb(33,37,41)",
          cardBgColor: "rgba(33,37,41, 0.25)",
          secondary: 'orange',
          titleColor: "black",
          titleColorActive: "black",
        }}
      />
    </section>
  );
}

export const experienceSectionId = "experienceSection";
